body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
 

.app {
  margin: 0 3em;
  padding: 0 3em;
  font-family: 'Comfortaa', cursive; }

.app__header {
  margin-bottom: 50px; }

.app__title {
  background: black;
  margin: 0;
  padding: 10px 20px;
  text-align: center;
  color: #00cec9; }

.app__filter {
  background: #414141;
  padding: 5px; }

.app__filter-full-name {
  border: 0;
  padding: 10px;
  font: inherit;
  width: 100%;
  box-sizing: border-box;
  color: #16a085;
  font-weight: bold;
  font-size: 18px;
  outline: none; }

.app__list {
  margin: 0;
  padding: 0;
  list-style: none; }

.app_list-link {
  text-decoration: none;
  color: #414141; }

.app__list-item + .app__list-item {
  margin-top: 16px;
  padding-top: 16px;
  border-top: 1px solid #00b894; }

.person {
  text-decoration: none;
  position: relative;
  padding-top: 2%;
  padding-left: 20%;
  min-height: 55px; }

.person__name {
  text-decoration: none;
  text-transform: capitalize;
  margin: 0 0 10px;
  font-size: 22px; }

.person__image {
  position: absolute;
  border-radius: 50%;
  left: 0;
  top: 0; }

.person__age,
.person__city {
  display: inline;
  font-size: 16px; }

.person__age::after {
  content: ',';
  padding-right: .3em; }

.person__city {
  text-transform: capitalize; }

.person--detail {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  text-align: center;
  padding-left: 0;
  height: 60vh; }
  .person--detail .person__image {
    position: static;
    margin: 0 auto 24px; }

.app__go-back-link {
  text-decoration: none;
  margin-left: 46%;
  margin-top: 30%;
  color: #00b894;
  border: 1px solid #414141; }

.app__footer {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  background-color: black;
  margin-top: 10px; }

.footer__text {
  text-transform: uppercase;
  color: #00b894; }

